import React from "react";
import classes from "./Post.module.css";

const Post = (props) => {
  return (
    <div className={classes.post__item}>
      <div className={classes.avatar}>
        <img src="data:image/png;base64," alt="avatar"/>
      </div>
      <div className={classes.post__text}>
        {props.message}
      </div>
      <div className={classes.like}>Like: {props.like}</div>
    </div>
  )
}

export default Post;